.action{
  position: fixed;
  bottom: 3.5em;
  right: 0;
  border-radius: 1em;
  background: rgba(47, 54, 41, 0.20);
  padding: 1.875em 1.6em;
  z-index: 5;
  gap: 2.5em;
  display: flex;
  flex-direction: column;
  &__btn{
    width: 4em;
    height: 4em;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    svg{
      width: 2.25em;
    }
  }
}

.footer{
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .action{
    background: rgba(47, 54, 41, 0.60);
    backdrop-filter: blur(2px);
    padding: 1em .75em;
    gap: 1em;
    right: .5em;
    &__btn{
      width: 3em;
      height: 3em;
      background-color: #FEFDF6;
      svg{
        width: 1.4em;
      }
      path{
        fill: $primary;
      }
    }
  }
}


